
@font-face {
  font-family: fangsong;
  src: url("assets/Wyue-GutiFangsong-NC.otf") format("opentype");
}
html,
body {
  height: 100%;
  margin: 0;
}
#app {
  /* font-family: 'Avenir', Helvetica, Arial, sans-serif; */
  height: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* background-color: #000000; */
  background-image: "";
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  /* text-align: center;
  color: #2c3e50;
  margin-top: 60px; */
}
#scroll {
  /* object-fit: cover; */
  height: 100%;
  width: 100%;
  /* background: url("./assets/landing.jpg"); */
  background-size: cover;
  background-position: right;
  /* animation: fadeIn 1s; */
  /* animation-fill-mode: forwards; */
  /* overflow: hidden; */
}
#cover {
  height: 100%;
  width: 100%;
}
#coverhover {
  height: 100%;
  width: 100%;
  display: none;
  overflow: hidden;
}
#coverhover:hover {
  height: 100%;
  color: white;
  width: 100%;
  /* background-image: linear-gradient(90deg, rgba(255,255,255,0) 0%, rgba(38,15,0,0.26) 45%, rgba(38,15,0,0.73) 57%, rgba(38,15,0,0.9251050762101716) 70%)!important; */
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(255, 255, 255, 0)),
    color-stop(45%, rgba(0, 0, 0, 0.26)),
    color-stop(57%, rgba(0, 0, 0, 0.73)),
    color-stop(70%, rgba(0, 0, 0, 0.9251050762101716))
  ) !important;
  background-image: linear-gradient(
    90deg,
    rgba(255, 255, 255, 0) 0%,
    rgba(0, 0, 0, 0.26) 45%,
    rgba(0, 0, 0, 0.73) 57%,
    rgba(0, 0, 0, 0.9251050762101716) 70%
  ) !important;
}
p {
  margin: 10px;
}
#title {
  padding-top: 50px;
  padding-left: 60%;
  padding-right: 4%;
  font-family: fangsong;
  font-size: 80px;
  height: 100%;
  opacity: 0;
  overflow: hidden;
  display: none;
}
#title:hover {
  opacity: 1;
}
#en_title {
  font-size: 40px;
}
#desc {
  margin-right: 20%;
  margin-top: 15px;
  font-size: 22px;
  text-align: justify;
  text-justify: inter-ideograph;
}
.button {
  margin-left: 2%;
  border-radius: 0;
  /* padding: 40px; */
  padding: 10px 25px 10px 25px;
  font-size: 22px;
  color: #ffffff;
  border: solid #ffffff 1px;
  text-decoration: none;
  display: inline-block;
  text-align: center;
}
.button:hover {
  background: #ffffff;
  color: rgba(0, 0, 0, 1);
  border-radius: 0;
  text-decoration: none;
}



